import React, { Component, ReactHTMLElement } from 'react';
import { checkPropTypes } from 'prop-types';
import { chosen_words, logEvent } from './App';

interface Props {
    subject: string,
    sender: string,
    message: string,
    pred: string,
    explain: string[],
    redraw: () => void,
    feedback: boolean,
}

let chosen_indexes: number[] = []

export default class Email extends React.Component<Props, {}> {

    private alreadyShown: string[] = [];

    constructor(props: Props) {
        super(props)
    }

    private clean(s: string): string {
        s = s.trim();
        s = s.replace(/[^0-9a-zA-Z\-']/g, '')
        s = s.replace(/\./, '')
        s = s.toLowerCase();
        return s;
    }

    private check(idx: number, s: string): number {
        s = this.clean(s);
        for (let k = 0; k < chosen_indexes.length; k++) {
            if (chosen_indexes[k] == idx) {
                return 2;
            }
        }
        for (let k = 0; k < this.props.explain.length; k++) {
            if (s == this.props.explain[k]) {
                if(this.alreadyShown.indexOf(s) > -1) {
                    // only show first instance
                    return 0;
                }
                this.alreadyShown.push(s);
                return 1;
            }
        }
        return 0;
    }

    public addChosen(idx: number, s: string) {
        if (!this.props.feedback) {
            return;
        }
        console.log("cleaning " + idx + " with " + s)
        s = this.clean(s);
        console.log("adding " + idx + " with " + s)
        let k = chosen_words.indexOf(s);
        let ki = chosen_indexes.indexOf(idx);
        if (ki == -1) {
            for (let k = 0; k < chosen_words.length; k++) {
                if (s == chosen_words[k]) {
                    logEvent("repeated " + s);
                    alert("Please select 3 different words.\nIf you change your mind on a word, you can click it again to deselect it.");
                    return;
                }
            }
        }
        if (k > -1) {
            chosen_words.splice(k, 1);
            chosen_indexes.splice(ki, 1);
            logEvent("toggled_off " + s);
        } else {
            if (chosen_words.length == 3) {
            logEvent("already_three " + s);
                alert("You have already chosen the required 3 words.\nIf you change your mind on a word, you can click it again to deselect it.");
                return;
            }
            logEvent("toggled_on " + s);
            chosen_words.push(s);
            chosen_indexes.push(idx);
        }
        // this.forceUpdate();
        this.props.redraw();
    }

    public render() {
        let subj: JSX.Element[] = [];
        let msg: JSX.Element[] = [];
        const me = this;
        // TODO: split by whitespace
        let type = "email-word" + (this.props.feedback ? "" : "-nof")
        let i = 0;
        this.alreadyShown = [];
        this.props.subject.split(" ").forEach(s => {
            const idx = i++;
            let c = me.check(idx, s);
            if (c == 1) {
                subj.push(<span key={idx + "-word"} className={type + " email-subj-explain"} onClick={() => { me.addChosen(idx, s) }}>{s}</span>);
            } else if (c == 2) {
                subj.push(<span key={idx + "-word"} className={type + " email-subj-chosen"} onClick={() => { me.addChosen(idx, s) }}>{s}</span>);
            } else {
                subj.push(<span key={idx + "-word"} className={type} onClick={() => { me.addChosen(idx, s) }}>{s}</span>);
            }
            subj.push(<span key={idx + "-wordsp"}>&nbsp;</span>);
        });
        this.props.message.split(" ").forEach(s => {
            let sp = s.split("\n")
            for (let k = 0; k < sp.length; k++) {
                const idx = i++;
                let s2 = sp[k];
                let c = me.check(idx, s2);
                if (c == 1) {
                    msg.push(<span key={idx + "-word"} className={type + " email-subj-explain"} onClick={() => { me.addChosen(idx, s2) }}>{s2}</span>);
                } else if (c == 2) {
                    msg.push(<span key={idx + "-word"} className={type + " email-subj-chosen"} onClick={() => { me.addChosen(idx, s2) }}>{s2}</span>);
                } else {
                    msg.push(<span key={idx + "-word"} className={type} onClick={() => { me.addChosen(idx, s2) }}>{s2}</span>);
                }
                if (k < sp.length - 1) {
                    msg.push(<span key={idx + "-wordnl"}>{"\n"}</span>);
                }
            };
            msg.push(<span key={s + i + "wordsp"}>&nbsp;</span>)
        });
        return (<div className="email-wrap">
            <div className="email">
                {this.props.pred.length > 0 && <div className="email-subject"><div className="badge">{"Model Prediction: " + this.props.pred}</div></div>}
                <div className="email-subject">Subject: {subj}</div>
                {/* <div className="email-sender"><strong>From: </strong>{this.props.sender}</div> */}
                <div className="email-message">{msg}</div>
            </div>
        </div>);
    }

}
