import React, { Component } from 'react';

export default class Prediction extends React.Component<{pred: string}, {}> {

    constructor(props: {pred: string}) {
        super(props)
    }

    public render() {
        return (<div className="pred">
            <span className="pred-q">The model decided this email is about </span>
            {/* <div className="pred-label">The model decided...</div> */}
            <span className="badge">{this.props.pred}</span>
            <span className="pred-q"> , which may or may not be correct.</span>
        </div>);
    }

}