import React, { Component } from 'react';
import { instance_correct, app, test_feedback } from './App';

export default class InstanceFeedback extends React.Component<{}, {}> {

    constructor(props: {}) {
        super(props)
    }

    public render() {
        let incorrect = instance_correct[0] == 0 ? "selected" : "";
        let correct = instance_correct[0] == 1 ? "selected" : "";
        const me = this;
        // console.log("instcor" + instance_correct[0])
        return (<div className="feedback-instance">
            <div className="feedback-instance-label">Please provide feedback to the model by telling it whether the decision (baseball or hockey) is correct.</div>
            <div className={"feedback-instance-button feedback-instance-correct " + correct} onClick={() => { instance_correct[0] = 1; app.forceUpdate(); }}>Correct</div>
            <div className={"feedback-instance-button feedback-instance-incorrect " + incorrect} onClick={() => { instance_correct[0] = 0; app.forceUpdate(); }}>Incorrect</div>
        </div>);
    }

}

export class TestFeedback extends React.Component<{}, {}> {

    constructor(props: {}) {
        super(props)
    }

    public render() {
        const q1 = "First, do you think this email is about hockey or baseball?";
        const q2 = "Second, what do you think the model will predict this email is about?";

        let hockey = test_feedback[0] == 0 ? "selected" : "";
        let baseball = test_feedback[0] == 1 ? "selected" : "";
        let model_hockey = instance_correct[0] == 0 ? "selected" : "";
        let model_baseball = instance_correct[0] == 1 ? "selected" : "";
        const me = this;
        console.log("instcor" + instance_correct[0])
        return (<>
            <div className="feedback-instance">
                <div className="feedback-instance-label">{q1}</div>
                <div className={"feedback-instance-button feedback-instance-hockey " + hockey} onClick={() => { test_feedback[0] = 0; app.forceUpdate(); }}>Hockey</div>
                <div className={"feedback-instance-button feedback-instance-baseball " + baseball} onClick={() => { test_feedback[0] = 1; app.forceUpdate(); }}>Baseball</div>
            </div>
            <br />
            <div className="feedback-instance">
                <div className="feedback-instance-label">{q2}</div>
                <div className={"feedback-instance-button feedback-instance-correct " + model_hockey} onClick={() => { instance_correct[0] = 0; app.forceUpdate(); }}>Hockey</div>
                <div className={"feedback-instance-button feedback-instance-incorrect " + model_baseball} onClick={() => { instance_correct[0] = 1; app.forceUpdate(); }}>Baseball</div>
            </div>
        </>);
    }

}