import React from 'react';
import { instance_correct, submit, chosen_words, no_feedback } from './App';

export default class FeedbackUs extends React.Component<{ practice: boolean, test: boolean, last: boolean }, { currChoice: string, showPopup: boolean }> {

    constructor(props: { practice: boolean, test: boolean, last: boolean }) {
        super(props)
        this.state = { currChoice: "", showPopup: false }
    }

    public render() {
        const me = this;
        // console.log(me.state.showPopup);
        let ready = no_feedback || instance_correct[0] != -1 || chosen_words.length == 3;
        if (this.props.test) {
            ready = instance_correct[0] != -1;
        }
        return (
            <>
                <div className={"us-button next-email " + (ready ? "ready" : "")} onClick={() => { if (ready) me.setState({ showPopup: true }); }}>{this.props.practice ? "Complete Practice" : this.props.last ? "Continue" : "Proceed to Next Email"}</div>
                {this.state.showPopup && <div className="full-us" onClick={() => { me.setState({ showPopup: false }) }}>
                    <div className="full-center">
                        <div className="full-center-inner" onClick={(e) => e.stopPropagation()}>
                            <div className="x-button" onClick={() => {
                                me.setState({ showPopup: false });
                            }}>X</div>
                            <span>{this.props.test ? "Let us know, do you think this email is about hockey or baseball?" : <>To help us improve this survey, please let us know: <br />Was the model's decision in this case correct?</>}</span>
                            <div className="invis-divider"></div>
                            <div className={"standard-button horizontal-margin" + (this.state.currChoice == "correct" ? " selected" : "")}
                                onClick={() => {
                                    submit("correct");
                                }}
                            >Yes</div>
                            <div className={"standard-button horizontal-margin" + (this.state.currChoice == "incorrect" ? " selected" : "")}
                                onClick={() => {
                                    submit("incorrect");
                                }}
                            >No</div>
                            <div className={"standard-button horizontal-margin" + (this.state.currChoice == "unsure" ? " selected" : "")}
                                onClick={() => {
                                    submit("unsure");
                                }}
                            >Not Sure</div>
                        </div>
                    </div>
                </div>}
            </>
        )
    }

}